/* global algoliasearch */

import instantsearch from 'instantsearch.js';
import {
  searchBox,
  hits,
  refinementList,
  pagination,
} from 'instantsearch.js/es/widgets';
import { createInsightsMiddleware } from 'instantsearch.js/es/middleware';

const searchClient = algoliasearch(
  'latency',
  '6be0576ff61c053d5f9a3225e2a90f76'
);

const search = instantsearch({
  indexName: 'instant_search',
  searchClient,
});

search.addWidgets([
  searchBox({
    container: '#searchbox',
  }),
  hits({
    container: '#hits',
    templates: {
      item: hit => `
        <article>
          <h1>${instantsearch.highlight({ attribute: 'name', hit })}</h1>
          <p>${instantsearch.highlight({ attribute: 'description', hit })}</p>
        </article>
      `,
    },
  }),
  refinementList({
    container: '#brand-list',
    attribute: 'brand',
  }),
  pagination({
    container: '#pagination',
  }),
]);

search.EXPERIMENTAL_use(createInsightsMiddleware({ insightsClient: false }));
search.start();
